.navigatorWrapper {
    width: 100%;
    height: 100%;
}

.centerBox {
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigatorContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: white;
    align-items: center;
    justify-content: center;
}

.responsive-logo {
    position: relative;
    min-width: 5px;
    max-width: 100%;
    height: auto;
}

#naviItemsWrapper{
    display: flex;
    flex-direction: row;
}

.text {
    color: #585555;
    cursor: context-menu;
}

.textBold {
    font-weight: bold;
}

.naviTimeWrapper{
    color: #585555;
    text-decoration: none;
    transition: 0.3s;
}

.naviTimeWrapperSelected{
    color: #F21B54;
    text-decoration: none;
}

.naviTimeWrapper:hover{
    color: #F21B54;
}
